*{
    margin: 0;
    padding:0;
    box-sizing: border-box;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}
.ScrollContainer{
    height: 100vh;
    overflow-y: scroll;
    display: flex;
    justify-content: center;
    align-items: center;
    h1{
        color: white;
        font-size: 5rem;
    }
}
.Header{
    top:0;
    left: 0;
    position: fixed;
    width: 100%;
    height: 4rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: cadetblue;
    color: white;
    opacity: 0.5;
}

.show{
    margin: auto;
    opacity: 1;
    justify-content: center;
    transform: translateX(0);
    transition: 1s;
}

.hidden{
    opacity: 0;
    filter: blur(5px);
    transition: .4s;
    transform: translateX(-90%);
}

@media(prefers-reduced-motion){
    .hidden{
        transition: none;
    }
}